body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.nav {
    width: 100vm;
    z-index: 14;
    display: flex;
    height: 90px;
    position: relative;
}

.show-nav {
    -webkit-animation: slide-nav-down 0.4s;
            animation: slide-nav-down 0.4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.hide-nav {
    -webkit-animation: slide-nav-up 0.8s;
            animation: slide-nav-up 0.8s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.nav-content {
    width: 100%;
    height: 90px;
    display: flex;
    margin: 2px 25px 10px 15px;
    padding: 10px;
    @media screen and (max-width: 768px) {
      display: none;
    }
}

.nav-items {
    width: 100%;
    padding-top: 17px;
    padding-bottom: 35px;
    display: flex;
    position: relative;
    justify-content: right;
}

.nav-item {
    position: relative;
    display: flex;
    color: #fff;
    cursor: pointer;
    text-decoration: none;
    padding: 0.3rem 1.5rem;
}

.logo {
    position: relative;
    width: 4rem;
    height: 4rem;
    display: table-cell;
    vertical-align: middle;
    transition: -webkit-transform 50s ease-in;
    transition: transform 50s ease-in;
    transition: transform 50s ease-in, -webkit-transform 50s ease-in;
}

.logo:hover {
    -webkit-transform: rotateY(99999deg);
            transform: rotateY(99999deg);
}

.underline::after {
    content: '';
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #0087ca;
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.underline:hover::after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}

@-webkit-keyframes slide-nav-up {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    100% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}

@keyframes slide-nav-up {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    100% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
}

@-webkit-keyframes slide-nav-down {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes slide-nav-down {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}
@-webkit-keyframes slidedown {
    0% {
      -webkit-transform: translateY(-250%);
              transform: translateY(-250%);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

@keyframes slidedown {
    0% {
      -webkit-transform: translateY(-250%);
              transform: translateY(-250%);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

@-webkit-keyframes slideup {
    0% {
      -webkit-transform: translateY(250%);
              transform: translateY(250%);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

@keyframes slideup {
    0% {
      -webkit-transform: translateY(250%);
              transform: translateY(250%);
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
}

@-webkit-keyframes slideleft {
    0% {
      -webkit-transform: translateX(-250%);
              transform: translateX(-250%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
}

@keyframes slideleft {
    0% {
      -webkit-transform: translateX(-250%);
              transform: translateX(-250%);
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
}

@-webkit-keyframes show-quick {
    0% {
      opacity: 0;
    }
    99% {
        opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

@keyframes show-quick {
    0% {
      opacity: 0;
    }
    99% {
        opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

@-webkit-keyframes slow-fade {
    0% {
      opacity: 0;
    }
    70% {
        opacity: 0;
    }
    100% {
      opacity: 1;
    }
}

@keyframes slow-fade {
    0% {
      opacity: 0;
    }
    70% {
        opacity: 0;
    }
    100% {
      opacity: 1;
    }
}


.nav-slidedown:nth-child(1) {
    -webkit-animation: slidedown 0.5s linear 0s, show-quick 0s linear 0s;
            animation: slidedown 0.5s linear 0s, show-quick 0s linear 0s;
}

.nav-slidedown:nth-child(2) {
    -webkit-animation: slidedown 0.5s linear 0.2s, show-quick 0.21s linear 0s;
            animation: slidedown 0.5s linear 0.2s, show-quick 0.21s linear 0s;
}

.nav-slidedown:nth-child(3) {
    -webkit-animation: slidedown 0.5s linear 0.4s, show-quick 0.41s linear 0s;
            animation: slidedown 0.5s linear 0.4s, show-quick 0.41s linear 0s;
}

.nav-slidedown:nth-child(4) {
    -webkit-animation: slidedown 0.5s linear 0.6s, show-quick 0.61s linear 0s;
            animation: slidedown 0.5s linear 0.6s, show-quick 0.61s linear 0s;
}

.logo-slideleft {
    -webkit-animation: slideleft 0.5s linear 0.3s, show-quick 0.31s linear 0s;
            animation: slideleft 0.5s linear 0.3s, show-quick 0.31s linear 0s;
}

.social-slideup {
    -webkit-animation: slideup 0.5s linear 0.35s, show-quick 0.36s linear 0s;
            animation: slideup 0.5s linear 0.35s, show-quick 0.36s linear 0s;
}

.titletext-fade {
    -webkit-animation: slow-fade 2s;
            animation: slow-fade 2s;
}


#name {
